.search-form {
  width: 100%;
  margin: auto;
  position: relative; }

.search-form__input {
  z-index: 1;
  width: 100%;
  padding: 4px;
  border: 1px solid #222;
  font-size: 16px;
  border-radius: 6px;
  font-family: inherit;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  text-align: center;
  line-height: 1.4rem; }

.search-form__input::-webkit-input-placeholder {
  color: #999;
  font-size: 0.7rem;
  text-align: center; }

.search-form__input:focus {
  outline: none;
  -webkit-box-shadow: 0px 0px 10px 0px #ccc;
          box-shadow: 0px 0px 10px 0px #ccc; }

[data-is-valid="false"] {
  -webkit-animation: blink 1s;
          animation: blink 1s; }

@-webkit-keyframes blink {
  0% {
    background-color: #ffd3d3; }
  100% {
    background-color: #fff; } }

@keyframes blink {
  0% {
    background-color: #ffd3d3; }
  100% {
    background-color: #fff; } }

.search-form__button {
  position: absolute;
  font-size: 0.8rem;
  top: 0.15rem; }



